<template>
    
<div class="modal-original-image" @click="onShowHideOriginal">
    <div class="modal-original-image-content">
        <div class="modal-original-image-header">
            <h2>{{ imageData.title }} by <a href="#" @click="onOnlyImgFromUser(imageData.owner)">{{ imageData.name }}</a></h2>
            <div class="btn-del-image" v-if="userId === imageData.owner" @click="delImage">Delete</div>
        </div>
        <img class="original-image" :src="imageData.url">
    </div>
</div>   

</template>

<script>
export default {
    props: ['onShowHideOriginal', "imageData", "userId", "refreshHome", "onOnlyImgFromUser"],
    data: function() {
        return {
            imageUrl: ""
        };
    },
    methods: {
        delImage: function() {
            
            this.$http.delete("/api/images/" + this.imageData.id)
                .then((response) => {
                    if(response.data === "success") {
                        this.refreshHome();
                    }
                });
        }
    }
};    
</script>

<style>
.modal-original-image {
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.75); /* Black w/ opacity */
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%; /* Full height */
    left: 0;
    position: fixed;
    top: 0;
    width: 100%; /* Full width */
    z-index: 10; /* Sit on top */
}

.modal-original-image-content {
    background: #2C302F;
    max-height: 500px;
    overflow: auto; /* Enable scroll if needed */
    padding: 30px;
}

.modal-original-image-header {
    align-items: center;
    display: flex;
    justify-content: space-between;
}

.btn-del-image {
    color: #D46279;
    cursor: pointer;
    padding: 3px 5px;
    transition: all 0.3s;
}

.btn-del-image:hover {
    background: #C42847;
    color: #fff;
}

.original-image {
    margin: 10px auto;
}

</style>


