<template>
    <div id="app">
        <link href="https://fonts.googleapis.com/css?family=Droid+Serif" rel="stylesheet">

        <app-header :user="user" :userId="userId" :onRefreshHome="refreshHome" :onShowHideModal="showHideModal" :onOnlyImgFromUser="onlyImgFromUser"></app-header>

        <div class="main">
          <one-image-container v-for="image in images" :execMasonry="execMasonry" :imageData="image" :key="image.id" :onShowHideOriginal="showHideOriginal" ></one-image-container>
        </div>
        
        <modalShowOriginal v-if="showOriginalSize" :onOnlyImgFromUser="onlyImgFromUser" :refreshHome="refreshHome" :onShowHideOriginal="showHideOriginal" :imageData="imageOriginalSize" :userId="userId"></modalShowOriginal>
        <modalAddImage v-if="showModal" :onShowHideModal="showHideModal"></modalAddImage>
    </div>
</template>

<script>
import Masonry from "masonry-layout";
import imagesLoaded from "imagesloaded";
import Header from "./components/Header.vue";
import OneImage from "./components/One-image.vue";
import AddImage from "./components/Add-image.vue";
import ShowOriginalSize from "./components/Show-original.vue";

export default {
  name: 'app',
  data () {
    return {
      user: "",
      userId: "",
      msg: 'Welcome to Your Vue.js App',
      images: "",
      showModal: 0,
      showOriginalSize: 0,
      imageOriginalSize: ""
    };
  },
  components: {
      appHeader: Header,
      oneImageContainer: OneImage,
      modalAddImage: AddImage,
      modalShowOriginal: ShowOriginalSize
  },
  methods: {
    execMasonry: function() {
      //console.log("masonry");
      var elem = document.querySelector('.main');
      var msnry = new Masonry(elem, {
        // options
        itemSelector: '.one-image-container',
        columnWidth: 270
      });
      
      msnry.once('layoutComplete', () => {
        elem.classList.add('load');
      });
      
      imagesLoaded( elem ).on( 'progress', function() {
        // layout Masonry after each image loads
        msnry.layout();
      });
      //msnry.layout();
    },
    showHideModal: function() {
      this.showModal = !this.showModal;
    },
    showHideOriginal: function(imageUrl) {
      this.showOriginalSize = !this.showOriginalSize;
      this.imageOriginalSize = imageUrl;
    },
    onlyImgFromUser: function(user) {
      this.$http.get("/api/profile/" + user)
        .then((response) => {
          this.images = response.data;
        });
    },
    refreshHome: function() {
      this.$http.get("/api/all")
        .then((response) => {
          this.images = response.data;
        });
    }
  },
  created: function() {
    this.refreshHome();
    this.$http.get("/api/check-user")
      .then((response) => {
          this.user = response.data.name;
          this.userId = response.data.twitter_id;
      });
  },
  mounted: function() {
    this.$nextTick(() => {
      this.execMasonry();
    });
  }
};
</script>

<style>
body {
  background-color: #2C302F;
  color: #E5E7E6;
  font-family: 'Droid Serif', serif;
  font-size: 1.25em;
  margin: 0;
}
a {
  color: inherit;
}
.main {
  margin: auto;
  padding: 85px 15px;
  width: 90%;
}
</style>



