<template>
    <div class="one-image-container">
        <div class="one-image" @click="onShowHideOriginal(imageData)">
            <span class="like" @click.stop="sendLike">Likes: {{ numLikes }}</span>
            <img :src="imageData.url">
            <div>
                <span class="one-image__title">
                    {{ imageData.title }}
                </span>
                <span class="one-image__author">
                    by {{ imageData.name }}
                </span>
            </div>
            
        </div>
    </div>
</template>

<script>
export default {
    data: function() {
        return {
            numLikes: ""
        };
    },
    mounted: function() {
        this.execMasonry();
        this.countLikes();

    },
    props: ["imageData", "onShowHideOriginal", "execMasonry"],
    methods: {
        showOriginal: function() {
            this.originalImage = !this.originalImage;
        },
        sendLike: function() {
            this.$http.post("/api/like", {imageId: this.imageData.id})
            .then((response) => {
                //The server can return response.data as "dec" or "inc"
                if(response.data) {
                    this.countLikes(response.data);
                }
            })
            .catch((err) => {
                console.log(err);
            });
        },
        countLikes: function(action) {
            if (action === "inc") this.numLikes++;
            else if (action === "dec") this.numLikes--;
            else if (this.imageData.likes) {
                this.numLikes = this.imageData.likes;
            }
            else this.numLikes = 0;
        }
    }
};
</script>

<style>
.one-image-container {
    background-color: #080909;
    box-sizing: border-box;
    cursor: pointer;
    display: table;
    margin: 10px 0;
    padding: 15px;
    position: relative;
    width: 250px;
}

.like {
    border: 1px solid transparent;
    float: right;
    font-size: 0.75em;
    margin-bottom: 10px;
    padding: 3px;
    transition: all 0.3s;
}
.like:hover {
    background-color: #2C302F;
}

.one-image img {
    display: block;
    height: auto;
    margin-bottom: 5px;
    max-height: 1500px;
    position: relative;
    width: 100%;
}

.one-image {
    float: left;
    height: 100%;
    overflow: hidden;
    position: relative;
    text-align: center;
    width: 100%;
}
.one-image__title {
    font-size: 1.5em;
}

.one-image__author {
    color: #D0CFCF;
    font-size: 0.7em;
}
</style>



