<template>
    <div class="modal-add-image" @click="onShowHideModal">
        <div @click.stop class="modal-content">
            <h2>Add a new image</h2>
            <form action="/api/image" method="POST">
                <div><input type="text" name="imageTitle" placeholder="Title of the image" autocomplete="off" /></div>
                <div><input type="text" name="imageUrl" placeholder="Url" v-model="imageUrl" autocomplete="off" /></div>
                <input type="submit" value="Submit"/>
            </form>
            <img v-if="imageUrl" :src="imageUrl" @mouseover='showDetails'>
        </div>
    </div>
</template>

<script>
export default {
    props: ['onShowHideModal'],
    data: function() {
        return {
            imageUrl: ""
        };
    }
};
</script>

<style>
.modal-add-image {
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.75); /* Black w/ opacity */
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%; /* Full height */
    left: 0;
    overflow: auto; /* Enable scroll if needed */
    position: fixed;
    top: 0;
    width: 100%; /* Full width */
    z-index: 1; /* Sit on top */
}

.modal-content {
    align-items: center;
    background: #2C302F;
    display: flex;
    flex-direction: column;
    min-width: 400px;
    padding: 15px;
    width: 40%;
}

.modal-content img {
    height: auto;
    max-height: 1500px;
    margin-top: 20px;
    width: 100%;
}

.modal-content input[type="text"] {
    background: inherit;
    border: none;
    border-bottom: 1px solid #ccc;
    color: inherit;
    font-size: 1.5em;
    margin-bottom: 25px;
    opacity: 0.4;
    text-align: center;
}

.modal-content input[type="text"]:focus {
    opacity: 1;
    outline: none;
} 

.modal-content input[type="submit"] {
    background-color: #080909;
    border: none;
    color: inherit;
    cursor: pointer;
    font-family: inherit;
    font-size: 0.9em;
    opacity: 0.7;
    padding: 5px 7px;
    transition: opacity 0.3s;
} 

.modal-content input[type="submit"]:hover {
    opacity: 1;
} 
</style>


