<template>
    <div class="header">
        <div class="header-content">
            <a href="#" @click="onRefreshHome">THE CITADEL</a>
            <div v-if="user">
                {{ user }} | 
                <a href="#" @click="onOnlyImgFromUser(userId)">Your images</a> |
                <a href="#" @click="onShowHideModal">Add image</a> | 
                <a href="/auth/logout">Log out</a>
            </div>
            <div v-else>
                Welcome, guest | <a href="/auth/twitter">Login</a>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: ["user", "userId", "onShowHideModal", "onOnlyImgFromUser", "onRefreshHome"],
    beforeCreate: function() {
        this.$http.get("/api/check-user")
        .then((response) => {
            this.user = response.data.name;
        });
    }
};
</script>

<style>
.header {
    background: #E5E7E6;
    color: #2B292A;
    font-size: 1.5em;
    position: fixed;
    width: 100%;
    z-index: 1;
}

.header a {
    color: inherit;
    text-decoration: none;
}

.header-content {
    display: flex;
    justify-content: space-between;
    padding: 15px;
}
</style>



